import React from 'react';
import SkillsStyles from "./Skills.module.css";

const Graphic = () => {
  return (
    <div className={SkillsStyles.skillItem}>
    <div className={SkillsStyles.skillmain}>
    <h2>GRAPHIC</h2>
     </div>
     <div>
      <ul>
       <li>Photoshop, illustrator, Figma</li>
       <li>이미지 편집 웹 화면 구현 및  프로토타입 제작</li>
      </ul>
     </div>
   </div>
  );
};

export default Graphic;