import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';


import ProjectStyles from "./ProjectSwiperStyles.module.css";

import { Pagination, Navigation } from 'swiper/modules';

const ProjectSwiper3 = () => {
  const [swiperRef, setSwiperRef] = useState(null);

  let appendNumber = 4;
  let prependNumber = 1;

  const prepend2 = () => {
    swiperRef.prependSlide([
      '<div class="swiper-slide">Slide ' + --prependNumber + '</div>',
      '<div class="swiper-slide">Slide ' + --prependNumber + '</div>',
    ]);
  };

  const prepend = () => {
    swiperRef.prependSlide(
      '<div class="swiper-slide">Slide ' + --prependNumber + '</div>'
    );
  };

  const append = () => {
    swiperRef.appendSlide(
      '<div class="swiper-slide">Slide ' + ++appendNumber + '</div>'
    );
  };

  const append2 = () => {
    swiperRef.appendSlide([
      '<div class="swiper-slide">Slide ' + ++appendNumber + '</div>',
      '<div class="swiper-slide">Slide ' + ++appendNumber + '</div>',
    ]);
  };
  return (
    <div className={ProjectStyles.SwiperBox}>
    <div className={ProjectStyles.SwiperTxtBox}>
    <h3  className={ProjectStyles.SwiperTitle}>Team .AH-OH</h3>
    <div className={ProjectStyles.SubTxtBox}>
      <div className={ProjectStyles.SubTxt1}>
        <h3>TYPE</h3>
        <p>clone</p>
      </div>
      <div className={ProjectStyles.SubTxt2}>
        <h3>PERIOD</h3>
        <p>23.00.00 - 23.00.00</p>
      </div>
      <div className={ProjectStyles.SubTxt3}>
        <h3>ETC</h3>
        <p>팀 프로젝트</p>
      </div>
      <div className={ProjectStyles.SubTxt4}>
        <h3>Use Skill</h3>
        <ul>
          <li>javascript</li>
          <li>react</li>
          <li>등등</li>
        </ul>
      </div>
    </div>
    </div>
           <Swiper
        onSwiper={setSwiperRef}
        slidesPerView={3}
        centeredSlides={true}
        spaceBetween={17}
        loop={true}
        modules={[Pagination, Navigation]}
        className={ProjectStyles.projectSwiper}
      >
        <SwiperSlide  className={ProjectStyles.projectSwiperSlide}>Slide 1</SwiperSlide>
        <SwiperSlide className={ProjectStyles.projectSwiperSlide}>Slide 2</SwiperSlide>
        <SwiperSlide className={ProjectStyles.projectSwiperSlide}>Slide 3</SwiperSlide>
        <SwiperSlide className={ProjectStyles.projectSwiperSlide}>Slide 4</SwiperSlide>
      </Swiper>

    </div>
  );
};

export default ProjectSwiper3;