import React from 'react';
import MainStyles from "./Main.module.css";

const MainPage = () => {
  return (
    <container>
      <div  className={MainStyles.wrapper}>
      <div className={MainStyles.TxtBox}>
      <div className={MainStyles.MainTxt}>
      <h1 className={MainStyles.MainTitleTxt1}>FRONTEND</h1>
      <h1 className={MainStyles.MainTitleTxt2}>KWON JIMIN</h1>
      </div>
      <div  className={MainStyles.SubTxt}>
      <p className={MainStyles.MainSubTxt}>새로운 것을 좋아하며, <strong>도전</strong>을 두려워하지 않는 사람</p>
      <p className={MainStyles.MainSubTxt}>포기하지 않고 다양한 방향으로 <strong>문제 해결</strong>을 위해 노력하는 사람</p>
      <p className={MainStyles.MainSubTxt}>변화하는 <strong>트렌드</strong>에 맞춰 발 빠르게 배우는 권지민 입니다.</p>
      </div>
      </div>
      <p className={MainStyles.LeftTxt}>2024 . PORTFOLIO</p>
      <p className={MainStyles.RightTxt}>Big goals get big results.</p>
      <p className={MainStyles.BottomTxt}>BE BRAVE</p>
      </div>
    </container>
  );
};

export default MainPage; 