import React from 'react';
import SkillsStyles from "./Skills.module.css";

const Backend = () => {
  return (
    <div className={SkillsStyles.skillItem}>
     <div className={SkillsStyles.skillmain}>
     <h2>BACKEND</h2>
      </div>
      <div>
      <p>NODE.JS</p>
       <ul>
        <li>Express 서버 생성</li>
        <li>Express-Session,Express-Router</li>
        <li>Sha256을 사용해 원본 데이터를 변환시켜 암호화 </li>
        <li>axios를 사용해  Promise 기반의 HTTP 요청을 손쉽게 만들고 관리</li>
        <li>.env 를 이용해 환경 변수를 설정하여 정보를 안전하게 저장</li>
        <li>EJS와 Express를 함께 사용해 서버에서 HTML을 동적으로 생성</li>
       </ul>
      </div>
    </div>
  );
};

export default Backend;