import React from 'react';
import ToyProjectSwiper from './ToyProjectSwiper';

import ToyStyles from "./ToySwiperStyles.module.css";
const ToyProjectPage = () => {
  return (
    <div className={ToyStyles.wrapper}>
      <div className={ToyStyles.ToyProjectMainTxt}>
      <div className={ToyStyles.MainTxtBox}>
        <div>
        <h1>Toy</h1>
        <h1>project</h1>
        </div>
        <p>뭐라 써야할지 고민중</p>
      </div>
      </div>
      <ToyProjectSwiper />
    </div>
  );
};

export default ToyProjectPage;