import React from 'react';
import SkillsStyles from "./Skills.module.css";

const Tool = () => {
  return (
    <div className={SkillsStyles.skillItem}>
    <div className={SkillsStyles.skillmain}>
    <h2>TOOLS</h2>
     </div>
     <div>
      <ul>
       <li>GitHub, Git Pages, Git Organization, Notion, Discode</li>
       <li>GitHub 프로젝트 버전 관리, 백업, 복구, 로컬 폴더 연동</li>
       <li>Notion을 이용해 공동으로 일정관리 및 작업 기록</li>
      </ul>
     </div>
   </div>
  );
};

export default Tool;